if ($("[data-controller='HomeController']").length) {
    $(".testimonies-carousel").owlCarousel({
        items: 3,
        nav: false,
        responsive: {
            0: {
                items: 1,
                mouseDrag: true
            },
            768: {
                items: 2,
                stagePadding: 50,
                margin: 25
            },
            992: {
                items: 3,
                mouseDrag: true,
                stagePadding: 100,
                margin: 25
            }
        }
    });
}



